<script>
export default {
  name: "LunboTu",
  data(){
    return{


    }
  },
  mounted() {

    let oll = document.querySelector(".oll")
    let ull = document.querySelector(".ull")
    // alert(ull.scrollLeft, ull.clientLeft)
    let len = document.querySelector(".ull").children.length
    for (let i = 0; i < len; i++) {
      let li = document.createElement("li")
      oll.appendChild(li)
      li.setAttribute("index", i)
      li.style.height=10+"px"
      li.style.width=10+"px"
      li.style.float='left'
      li.style.cursor="pointer"
      li.style.margin=4+"px"
      li.innerText=i.toString()
      li.addEventListener("mouseenter", function () {
        clearTimeout(inte)
        let a = this.getAttribute('index');
        ull.style.marginLeft = (-1200) * a + "px"
        // setInterval(timer,1000)
      })
      // li.addEventListener("mouseover", function (){
      //     setInterval(timer,1000)
      //     console.log("1")
      // })
    }
    // let l = document.querySelector(".l")
    // let r = document.querySelector(".r")
    // r.addEventListener("click",function () {
    //     clearTimeout(inte)
    //     let width=ull.style.marginLeft
    //     width=width.slice(0, -2)
    //     if(width==(0)){
    //         width=-900
    //     }
    //     ull.style.marginLeft=width+300+"px"
    // })
    // l.addEventListener("click",function () {
    //     clearTimeout(inte)
    //     let width=ull.style.marginLeft
    //     width=width.slice(0, -2)
    //     if(width>=0){
    //         width=-1200
    //     }
    //     ull.style.marginLeft=width+300+"px"
    // })
    let x = 0
    let timer = function () {
      if (x < -2400) {
        x = 0
      }
      ull.style.marginLeft = x + 'px'
      x = x - 1200
    }
    let inte = setInterval(timer, 3000)

  }


}
</script>

