<template>
  <div v-show="$route.path!='/index'" class="breadcumb">
    <ul style="display: flex;justify-content: left;flex-direction: row">
      <li v-for="(v,i) in lists" :key="i">
        <router-link :to="{path:v.path}">{{v.meta.title}}</router-link>
        <span v-if="i<lists.length-1">/</span>
      </li>
    </ul>
  </div>

</template>

<script>
export default {
  name: "BreadCrumb",
  data(){
    return {
      lists:[]
    }
  },

  //监听路由变化
  watch:{
    $route(to,from){
      let matched = to.matched;   //this.$route.matched
      //这个地方是为了固定第一级目录是首页不变
      if(matched.length && matched[0].name!=="home"){
        matched = [...matched];
      }
      var arr = [1,2,3]
      var arr1 = [4,5]
      var arr2 = [2,...arr1] == [2,4,5]

      this.lists = matched;
    }
  }

}
</script>

<style scoped>
*{
  list-style: none;
}
</style>